.App{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:#282c34 ;
}
.container{
  display:flex ;
  height: 80vh;
  border: 1px solid grey;
  color: whitesmoke;

 padding-top: 20px;
 flex-direction: column;
 align-items: center;
 font-size: 20px;
}
.todoform{
  display: flex;
  justify-content: space-around;
  width: 450px;

}
input{
  width: 75%;
  padding: 10px;
  border-radius: 20px;
  border: none;
}
 button{
  border:none;
  padding: 10px;
  border-radius: 10px;
  margin: 0 5px;
 
 }
 .allTodos{
  list-style:none;
  margin: 0;
  padding: 0;

 }
 .singleToDo{
  border-radius: 50px;
  margin: 10px 0px;
  background-color: #5dadf8;
  padding: 10px 20px;
  display: flex;
  width: 450px;
  align-items: center;
 }
 .texttodo{
  flex:1;
 }